/*
 * Define the hero styles.
 */

.hero {
  .container {
    position: relative;
    z-index: 10;
  }
}
.bio {
  margin: auto;
  max-width: 640px;
  font-size: 2.8rem;
  letter-spacing: -0.5px;

  @include min($tablet) {
    width: 55%;
  }
}
.bio .secondary_header {
  margin-bottom: 1em;
  letter-spacing: 3px;
  text-transform: uppercase;
  font-size: 1.5rem;
  font-weight: 300;
}
.container .spacer {
  border-top: 10px;
}
#bio {
  padding: 0em 0 8em;
  background: $grey;

  @include min($tablet) {
    padding: 0em 0 8em;
  }
}

.bio_text {
  line-height: 1.7;
  font-size: 1.8rem;

  @include min($tablet) {
    font-size: 2.2rem;
  }

  a {
    color: $primary-text;
  }
}

hr {
  border-top: 2px solid $dark-grey;
  padding:10px;
  margin-left: 35%;
  margin-right: 35%;
}

.hero__title {
  position: relative;
  max-width: 840px;
  margin-top: 0.5em;
  margin-bottom: 1.5em;
  letter-spacing: -1px;
  line-height: 1.7;
  font-size: 3rem;
  font-weight: 400;

  @include min($tablet) {
    margin-top: 1.25em;
    margin-bottom: 2.5em;
    line-height: 68px;
    font-size: $h1-font-size;
  }
  @include min($desktop) {
    max-width: 1200px;
  }
  @include min($large) {
    max-width: 840px;
  }
}
.hero__title a {
  color:$dark-grey;
  text-decoration: underline;
}

.hero__link {
  color: $primary-light;
  transition: color 250ms ease;

  &:hover,
  &:focus,
  &:active {
    color: $primary-text;
  }
}

.hero__link--second {
  color: $primary-dark;
  transition: color 250ms ease;

  &:hover,
  &:focus,
  &:active {
    color: $black;
  }
}

.hero__employer {
  margin-bottom: 2em;

  @include min($tablet) {
    flex: 1 1 0;
    margin-bottom: 0;
  }

  a {
    display: inline-block;
  }

  svg {
    height: auto;
    width: 200px;
    transition: all 200ms ease-in-out;

    &:hover {
      transform: translateY(-2px);
      opacity: 0.9;
    }
  }
}

.hero__img {
  margin-bottom: 1em;
  justify-content: center;
  @include min($tablet) {
    flex: 0.5 0.5 0;
    margin-bottom: 0;
  }

  @keyframes changehue {
    from {
      filter: sepia(100%) saturate(100%) hue-rotate(0deg);
      -webkit-filter: sepia(1) saturate(1.5) hue-rotate(0deg);
      -ms-filter: sepia(100%) saturate(100%) hue-rotate(0deg);    
    }
  
    to {
      filter: sepia(100%) saturate(100%) hue-rotate(360deg);
      -webkit-filter: sepia(1) saturate(1) hue-rotate(360deg);
      -ms-filter: sepia(100%) saturate(100%) hue-rotate(360deg);    
    }
  }
  img {
    height: auto;
    width: 150px;
    transition: all 200ms ease-in-out;
    border-radius: 50%;
    animation-duration: 60s;
    animation-name: changehue;
    animation-iteration-count: infinite;  


    &:hover {
      transform: translateY(-2px);
      opacity: 0.9;
    }
  }
}

.ss-logo {
  fill: $black;
}

.palantir-logo {
  margin-top: 1rem;

  @include min($tablet) {
    margin-top: 0;
    margin-left: 1rem;
  }
}

.discord-logo {
  fill: $black;
  position: relative;
  top: -10px;
}

.employer__label {
  margin-bottom: 1em;
  letter-spacing: 2px;
  text-transform: uppercase;
  font-size: 1.5rem;
  font-weight: 400;
}

